export class Dot {
    constructor(entry) {
        this.label = entry.label;
        this.active = entry.active;
        this.ring = entry.ring;
        this.moved = entry.moved;
        this.quadrant = entry.quadrant;
        this.link = entry.link;
    }
    getLabelFirstLetter() {
        return this.label.match(/[a-z]/i)[0];
    }
}
